.letterbox-content{
    max-width: 100rem;
    margin: 0 auto;
    height: 85vh;
}
.letterbox-title{
    text-align: center;
    font-size: 2.2rem;
}

/*------------------------ 작성버튼 -------------------------------*/

.lettersheet{
    width: 500px;
    height: 40vh;
    position: absolute;
    top: 30%; left:50%; transform:translateX(-50%);
    border: 1px solid black;
    background-image: url("../../img/letter1.png");
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 100;
}

.lettersheet input{
    color: black;
    font-size: 20px;
}
.rabbit{
    width: 22%;
    position: absolute;
    bottom: 20%; left:20%; transform:translateX(-50%);
}
.parmtree1{
    position: absolute;
    width: 18%;
    top: 42%; right:-10%; transform:translateX(-50%);
}
.parmtree2{
    position: absolute;
    width: 22%;
    bottom: 50%; left: -5%;
}
.moon{
    position: absolute;
    width: 10%;
    top: 13%; right:15%; transform:translateX(-50%);
}
/*------------------- 별 ------------------------------*/
.randStar{
    width: 4%; position: absolute; z-index: 100;
    animation: rotate 2.5s infinite;
}
@keyframes star{

    50%{
        background-position: left bottom;
        transform: rotate(90deg);
    }
    100%{
        transform: rotate(270deg);

    }
}